import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  expression = 'ExpressÃ£o booleana aqui';
  nv = 'Numero de variaveis';
  net = {};
  constructor(private http: HttpClient) { }

  capture() {
    if ( +this.nv > 9) { return; }

    let arr = [];
    if (this.expression[0] === '0' || this.expression[0] === '1') {
      for (let i = 0; i < this.expression.length; i++) {
        if (this.expression[i] === '1') {
          arr.push(Math.log(Math.pow(2, i)) / Math.log(2));
        }
      }
    } else {
      arr = this.translate();
    }
    this.http.post('https://zerone-app.herokuapp.com/solver', { nv: +this.nv, num: arr })
      .subscribe((response) => {
        this.expression = response['exp'];
      });
  }

  clear(key: string) {
    this[key] = '';
  }

  translate() {
    const num = [];
    const alphabet = ['A', 'B', 'C', 'D', 'E', 'F', 'G'];
    const trimmed = this.expression.replace(/\s/g, '');
    const arr = trimmed.split('+');
    const upperLimit = Math.pow(2, +this.nv);
    const table = {};

    for (let i2 = 1; i2 <= +this.nv; i2++) {
      const limit = upperLimit / Math.pow(2, i2);
      let counter = 0;
      let SWITCH = false;
      for (let i = (upperLimit - 1); i >= 0 ; i--) {
        let letter = alphabet[i2 - 1];

        if (counter === limit) {
          SWITCH = !SWITCH;
          counter = 0;
        }
        if (SWITCH) {
          letter = letter + `'`;
        }
        if (table[i] === undefined) {
          table[i] = letter;
        } else {
          table[i] = table[i] + letter;
        }
        counter++;
      }
    }
    arr.forEach(s => {
      for (let count = 0; count < upperLimit; count++) {
        if (this.isSubstring(table[count], s)) {
          if (!num.includes(count)) {
            num.push(count);
          }
        }
      }
    });
    num.sort((a, b) => (a - b));
    return num;
  }


  isSubstring(arr: string, arr2: string) {
    let index = 0;

    // tslint:disable-next-line:prefer-for-of
    for (let i = 0; i < arr2.length; i++) {
      let comp;
      if (arr2[i + 1] === `'`) {
        comp = arr2[i] + arr2[i + 1];
        i++;
      } else {
        comp = arr2[i];
      }

      index = arr.indexOf(comp, index);

      if (arr[index + 1] === `'` && arr2[i] !== `'`) {
        return false;
      } else {
        comp = arr2[i];
      }


      if (index === -1) {
        return false;
      }
    }
    return true;
  }
}
